* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
}

body {
  display: flex;
  align-items: center;
  flex-direction: column;
  background: rgb(34, 193, 195);
  background: linear-gradient(0deg, rgb(34, 193, 195) 0%, rgb(45, 122, 253) 100%);
  background-size: contain;
  background-position: center center;
  background-attachment: fixed;
  background-repeat: no-repeat;
}

.register {
  margin-top: 50px;
  text-align: center;
  color: white;
}

.register .container {
  display: flex;
  flex-wrap: wrap;
  margin-right: 10px;
  width: 500px;
  height: 500px;
}

.register .container h1 {
  justify-content: flex-start;
  margin-right: 10px;
  text-align: center;
  font-size: 40px;
  width: 250px;
  height: 60px;
}

.register .container a {
  width: 60px;
  height: 60px;
  margin-right: 10px;
}

.register .container a img {
  width: 60px;
  height: 60px;
}

.register .container input {
  border: none;
  outline: none;
  height: 40px;
  margin: 10px;
  margin-left: 20px;
  font-size: 20px;
  border-bottom: 1px solid black;
  background: transparent;
}

.register .container input::placeholder {
  color: white;
  font-size: 20;
  font-style: italic;
}

.register .container input[name=firstName] {
  width: 40%;
}

.register .container input[name=lastName] {
  width: 40%;
}

.register .container input[name=email] {
  width: 90%;
}

.register .container input[name=password] {
  width: 40%;
}

.register .container input[name=confirmPassword] {
  width: 40%;
}

.register .container h2 {
  font-size: 30px;
  font-family: Arial, Helvetica, sans-serif;
  margin-right: 25px;
  margin-left: 50px;
}

.register .container label {
  margin-left: 20%;
  font-size: 25px;
  padding-top: 8px;
}

.register .container input[type=radio] {
  color: #000;
  width: 20px;
  height: 20px;
  margin-right: 15px;
}

.register .container #dob {
  font: 27px sans-serif;
  font-style: italic;
  margin-left: 100px;
  margin-top: -10px;
}

.register .container input[type=date] {
  margin-top: -10px;
}

.register .container button {
  width: 360px;
  height: 50px;
  margin-left: 50px;
  border-radius: 3px;
  border-color: blue;
  background-color: white;
  font-size: 20px;
  position: relative;
  z-index: 5;
  transition: color 0.4s;
  letter-spacing: 1px;
}

.register .container button::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0%;
  top: 0;
  left: 0;
  border-radius: 0px;
  background: rgba(0, 4, 255, 0.89);
  transition: height 0.3s;
  z-index: -1;
}

.register .container button:hover {
  color: rgb(255, 255, 255);
  font-size: 25px;
}

.register .container button:hover::after {
  height: 100%;
}

@media screen and (max-width: 550px) {
  .register .container {
    width: 300px;
    height: 300px;
  }

  .register .container h1 {
    justify-content: center;
    margin-left: 10%;
  }

  .register .container a {
    margin-right: 40px;
    margin-left: 14%;
    margin-bottom: 15px;
  }

  .register .container h2 {
    margin-left: 30%;
    margin-right: 20%;
  }

  .register .container label {
    margin-left: 3%;
    margin-bottom: 10px;
  }

  .register .container #dob {
    margin-left: 100px;
    margin-top: -10px;
  }

  .register .container input[type=date] {
    margin-left: 25%;
    margin-bottom: 30px;
  }
}